*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: Georgia, 'Times New Roman', Times, serif;
  line-height: 1.5;
  
  color: black;
  width: auto;
  height: 100vh;

  background-repeat: no-repeat;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}
h1 {
  text-align: center;
  margin-top: 50px;
}

h2,
h3,
p {
  margin: 0;
}
i{
  font-weight: 600;
  font-size: 20px;
}
ul li{
  list-style-type: none;
}
.container{
  margin: 0 auto;
  padding: 0 10px;
  min-width: 360px;
}
@media screen and (min-width: 1024px) {
  .container{
    width: 100%;
  }
}
/* header */
.header {
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 1;
  background: #fff; 
  border-bottom: 1px solid #E5E5E5;

}

.header__nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  height: 49px;
}

.header__nav-menu{
  display: none;
}

.header__nav-reg{
  display: none;
}

.main__container{
  overflow-x: auto;
}
/* hero section */
.hero{
  background: #E2F2FF;
  border-radius: 10px;
  padding: 20px 10px;
  margin: 10px 0 40px 0;
}

.hero__title{
  font-family: 'Roboto';
  font-weight: 600;
  font-size: 32px;
  line-height: 120%;
  letter-spacing: 0.02em;
  color: #001E41;
  margin: 0 0 20px 0;
}

.hero__main-info{
  font-family: 'Roboto';
  font-size: 12px;
  line-height: 1.17;
  color: #001E41;
  margin-bottom: 10px;
}

.hero__aditional-info{
  font-family: 'Roboto';
  font-size: 12px;
  line-height: 1.17;
  color: #919191;
  margin-bottom: 20px;

}

.btn{
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
  letter-spacing: 0.0025em;
  padding: 10px 30px;
  margin: 0 auto 20px;
  display: flex;
  justify-content: center;
}
.hero__txt{
  font-family: 'Roboto';
  font-size: 12px;
  line-height: 1.17;
  text-align: center;
  color: #001E41;
  margin-bottom: 10px;
}

.hero__items{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
  gap: 20px 10px;
}

.hero__item{
  
}

.hero__img{
  position: relative;
  
}
.hero__img-first{
  position: absolute;
  top: -20px;
  left: 0;
  animation-name: example;
  animation-duration: 4s;
}

.hero__img-second{
  position: absolute;
  top: 45px;
  right: 0;
  animation-name: example;
  animation-duration: 4s;

}

.hero__img-third{
  position: absolute;
  bottom: 0;
  left: 0;
  animation-name: example;
  animation-duration: 4s;
}

@keyframes example {
  0% { transform: scale(0.2); opacity: 0; } 
  50% { transform: scale(1.5); opacity: 1; }
  100% { transform: scale(1); } 
}

.hero__img-main{
  margin: 0 auto;
}

/* slider section */
.card{
  padding: 15px;
  margin: 0 auto;
  width: 50%;
}
@media screen and (max-width: 767.98px){
  .search-form{
    flex-direction: column;
  }
  .form-inline .form-group{
    margin: 0 0 15px 0;
   
  }
}

.icon {
  width: 100%;
  height: 100%;
}

/*slider*/
.slider{
  min-width: 0;
}

.slider__title{
  font-family: 'Roboto';
  font-size: 20px;
  line-height: 130%;
  color: #919191;
  margin-bottom: 15px;
}
.slider-title-accent{
  font-family: 'Roboto';
  font-size: 20px;
  line-height: 130%;
  color: #001E41;
}

.swiper-wrapper{
  margin-bottom: 40px;
}

.swiper-slide{
  padding: 20px;
  background: #F8F8FA;
  border-radius: 10px;
}

.slider__item-title{
  font-family: 'Roboto';
  font-weight: 600;
  font-size: 17px;
  line-height: 150%;
  color: #001E41;
  margin-bottom: 20px;
}

.slider__item-info{
  font-family: 'Roboto';
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 0.0025em;
  color: #001E41;
}

.slider__item{
  min-height: 372px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.swiper-pagination{
  position: static;
  bottom: 0;
}

.swiper-button-next:after, 
.swiper-button-prev:after{
  display: none;
}

.slider__btn{
  margin-bottom: 10px;
}
.slider__bottom-info{
  font-family: 'Roboto';
  font-size: 12px;
  line-height: 1.17;
  text-align: center;
  color: #001E41;
  padding-bottom: 29px;
}

.slider__item-count{
  font-family: 'Roboto';
  font-weight: 900;
  font-size: 48px;
  line-height: 130%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  justify-content: end;
}

.slider__item::after{
  content: '';
  position: absolute;
  z-index: -1;
  width: 219px;
  height: 219px;
  right: 0;
  bottom: 0;
  background: url(./images/Ellipse.png) no-repeat;
  border-bottom-right-radius: 10px 10px;
  background-position: bottom right;
}

/* popup section */
.popup{
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 30, 65, 0.171);
  top: 0;
  left: 0;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
}

.popup:target {
  opacity: 1;
  visibility: visible;
}
.popup__body{
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup__content{
  background: #fff;
  max-width: 300px;
  padding: 8px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.08);
  border-radius: 11.25px;
}
.popup__close{
  width: 22.5px;
  height: 22.5px;
  background: #F3F4F6;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
}
.popup__main-content{
  padding: 10px 24px 26px 24px;
}
.popup__close-img{}
.popup__title{
  font-family: 'Roboto';
  font-size: 18px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.02em;
  color: #001E41;
  margin-bottom: 18px;

}
.popup__txt{
  font-family: 'Roboto';
  font-size: 12px;
  line-height: 150%;
  text-align: center;
  letter-spacing: 0.0025em;
  color: #001E41;
  margin-bottom: 18px;
}
.popup__form{

}

.popup__bottom{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
}

.popup__bottom-txt{
  font-family: 'Roboto';
  font-size: 9px;
  line-height: 11px;
  color: #BDBDBD;
}
.form-label{
  font-family: 'Roboto';
  font-weight: 600;
  font-size: 9px;
  line-height: 130%;
  display: flex;
  align-items: center;
  letter-spacing: 0.0025em;
  color: #919191;
}

/* .form-control {
  padding: 6px 9px;
} */
.form-control::placeholder{
  font-family: 'Roboto';
  font-size: 10.5px;
  line-height: 150%;
  display: flex;
  align-items: center;
  letter-spacing: 0.0025em;
  color: #BDBDBD;
}

.form-check-label{
  font-family: 'Roboto';
  font-size: 9.75px;
  line-height: 11px;
  color: #919191;
}

.form-check-label a{
  font-family: 'Roboto';
  font-size: 9.75px;
  line-height: 11px;
  color: #5D78FF;
}

.dropdown{
  display: flex;
}

.dropdown .form-control{
  width: 240%;
  font-family: 'Roboto';
  font-size: 10.5px;
  border-left: none;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}

.dropdown .form-control:focus{
  border-color: rgba(0,0,0,0.2);
  outline: 0;
  box-shadow: none;
}

.form-select{
  border-right: none;
  font-family: 'Roboto';
  font-size: 10.5px;
  line-height: 150%;
  display: flex;
  align-items: center;
  letter-spacing: 0.0025em;
  color: #001E41;
  border-bottom-left-radius: 6px;
  border-top-left-radius: 6px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  background-image: url(../src/images/select.svg);
  background-size: 4px 2px;
}

.form-select:focus{
  border-color: rgba(0,0,0,0.2);
  outline: 0;
  box-shadow: none;
}

.g-3{
  flex-direction: column;
}

.col-12{
  display: flex;
  gap: 8px;
}

.col-12 .btn{
  padding: 9px 20px;  
}

.col-md-4{
  width: auto;
}
.col-md-3{
  width: auto;
}
.invalid-feedback{
  font-family: 'Roboto';
  font-weight: 600;
  font-size: 9px;
  line-height: 130%;
  letter-spacing: 0.0025em;
  color: #dc3545;

}

@media screen and (min-width: 1024px) {
  .container{
    width: 100%;
  }

  .header__nav{
    height: 77px;
  }

  .header__nav-menu{
    display: flex;
    gap: 30px;
    margin-bottom: 0;
  }
  .header__nav-item{
    font-family: 'Source Sans Pro';
    font-weight: 600;
    font-size: 18px;
    line-height: 110%;
    color: #7D7D7D;
  }
  .header__nav-reg{
    display: flex;
    gap: 40px;
    align-items: baseline;
  }
  .header__nav-burger{
    display: none;
  }

  .header__nav-btn{
    font-family: 'Source Sans Pro';
    font-size: 18px;
    line-height: 160%;
    text-align: center;
    color: #5D78FF;
    background: none;
    border: none;
  }

  .header__nav-reg .btn {
    margin-bottom: 0;
  }

  .hero__content{
    display: flex;
  }

  .hero__title{
    font-weight: 600;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: 0.02em;
    text-align: start;
  }

  .hero__aditional-info{
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.0025em;
  }

  .hero__main-info{
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.005em;
    text-align: start;
  }

  .hero__txt{
    font-size: 16px;
    line-height: 150%;
    text-align: start;
    letter-spacing: 0.005em;
  }

  .hero__items{
    justify-content: flex-start;
  }

  .hero__img {
    width: 533px;
  }

  .hero__main{
    width: 665px;
    padding: 61px 0 10px 41px;
  }

  .hero__img-main{
    width: 370px;
  }

  .slider__title{
    font-size: 36px;
    line-height: 42px;
  }

  .slider-title-accent{
    font-size: 36px;
    line-height: 42px;
  }

  .slider__item-title{
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
  }

  .slider__item-info{
    font-size: 14px;
    line-height: 150%;
  }

  .slider__bottom-info{
    font-size: 14px;
    line-height: 130%;
    letter-spacing: 0.0025em;
  }

  .slider__item{
    max-width: 302px;
  }
}
